/* Variables
 * ===============
 *
 * All variables and most of the configuration is defined on this page. */


/* Containers */

$x-small: 600px;
$small: 800px;
$medium: 1000px;
$large: 1200px;

/* Breakpoints */

/*
$mobile: $x-small;
$desktop: $medium;
*/

/* CB: ridefinisco i breakpoint */
$x-small-bp: $x-small;
$small-bp: $small;
$medium-bp: $medium;
$large-bp: $large;

/* Colors */

$background: white; 
$primary-color: #028EB2;
$block-background: #dddddd;
$block-border: #bbbbbb;
$footer-background: #3F4C50;
$label-color: #bbbbbb;
$label-hover-color:#888888;

$secondary-color: #5FBA7D; 
$accent-color: #cdcdcd;
$alternate-background: #fafafa;
$alternate-color: #404040;
$link-color: $alternate-color;
/*$link-hover-color: darken($link-color, 15%);*/
$link-hover-color: $primary-color;
$highlight: #ffeea8;
$error: #D33C40;
$bq-border: 16px solid #f0f0f0;


/* Typography */

// Body font
//$font-size Ã¨ la dimensione applicata al selettore html
$font-size: 1rem;
$body-font-size: 0.875rem;
$font-style: normal;
$font-variant: normal;
$font-weight: normal;
$font-color: #000000;
//$font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
$font-family: Verdana, Geneva, sans-serif;
$line-height: 1.6;

// Headings
//$heading-font-color: #404040;
$heading-font-color: #028EB2;
//CB: introduco la variabile $heading-secondary-font-color
$heading-secondary-font-color: #333333;
$heading-font-weight: 300;
//$heading-font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
$heading-font-family: 'Yanone Kaffeesatz','Trebuchet MS', Helvetica, sans-serif;
$heading-line-height: 1.2;
//CB: introduco la variabile $heading-text-transform
$heading-text-transform: uppercase;

// Mobile heading font size
$h1-mobile: 1.5rem;/*1.75*/
$h2-mobile: 1.375rem;/*1.5*/
$h3-mobile: 1.25rem;
$h4-mobile: 1.1rem;
$h5-mobile: 1rem;

// Heading font size
$h1: 1.5rem;
$h2: 1.375rem;
$h3: 1.25rem;
$h4: 1.1rem;
$h5: 1rem;

/* Padding */
//CB ridefinisco padding da 1 a 0.001 a 0.375
$padding: 0.375rem;
$margins: 1.5rem;
$content-padding: 60px 0;
$content-padding-mobile: 30px 0;

/* Borders */

$border-width: 1px;
$border-style: solid;
$border-color: #dedede;
$border-radius: 4px;
$borders: $border-width $border-style $border-color;

/* Buttons */

$button-background: $primary-color;
$button-background-hover: darken($button-background, 10%);
$button-color: #ffffff;
$button-font-weight: 600;
//$button-font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
$button-font-family: Verdana, Geneva, sans-serif;
$button-font-size: 1rem;
$button-border-width: 1px;
$button-border-style: solid;
$button-border-color: $button-background;
$button-border-radius: $border-radius;
$button-text-transform: none;

// Accent buttons
$accent-button-background: $secondary-color;
$accent-button-color: #ffffff;
$accent-button-color-hover: #ffffff;

// Muted Buttons
$muted-border: 1px solid $accent-color;
$muted-border-hover: 1px solid darken($accent-color, 30%);
$muted-background: transparent;
$muted-background-hover: transparent;
$muted-color: darken($accent-color, 50%);
$muted-color-hover: darken($accent-color, 50%);

// Round Buttons
$round-buttons: 40px;

/* Forms */

$forms: ('[type=color], [type=date], [type=datetime], [type=datetime-local], [type=email], [type=month], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url], [type=week], [type=time], select, textarea');
$buttons: ('.button, a.button, button, [type=submit], [type=reset], [type=button]');
$input-background: transparent;
$placeholder: darken($accent-color, 20%);
$form-border: 1px solid #cdcdcd;
$form-border-hover: 1px solid darken(#cdcdcd, 10%);
$form-border-focus: 1px solid $link-color;

/* Tables */

$stripes: #f8f8f8;
$caption: #ababab;

/* Code */

$code-color: $font-color;
$code-size: 14px;
$code-family: Menlo, monospace;
$code-background: transparent;
$code-borders: $borders;